<template>
    <div>
        <headers :title="title"></headers>
        <div class="add-list">
           <div class="bg"></div>
        </div>
        <footers :footerData="footerData"></footers>
    </div>
    
</template>

<style lang="less" scoped>
    .add-list{
        font-family: "PingFang-SC-Medium";
        padding-top: 90px;
        background: #fff;
        .bg{
            position: fixed;
            left: 0;
            right: 0;
            top: 90px;
            bottom: 96px;
            background: url('../../assets/imgs/add-noData.png') no-repeat;
            background-size: 100% 100%;
        }
        
       
    }
</style>

<script>
import "lib-flexible"
import headers from '../../components/editHeader.vue'
import footers from '../../components/editfooter.vue'
export default {
  components:{
      headers,
      footers
  },
  data(){
      return{
        title: "地址列表",
        radio: '1',
        footerData: "新增地址"
      }
  },
  mounted(){
      document.body.style.backgroundColor = "rgb(247,246,247)";
  }
}
</script>





